import React from "react";
import { Row } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import DashboardNotebooks from "../../../components/custom/dashboard/DashboardNotebooks";
import DashboardPipelines from "../../../components/custom/dashboard/DashboardPipelines";
import DashboardRuns from "../../../components/custom/dashboard/DashboardRuns";
import image from "../../../assets/MLOps platform Welcome.png";
import slackLogo from "../../../assets/logos/slack-logo.png";
import ForumIcon from "@mui/icons-material/Forum";
import ReportIcon from "@mui/icons-material/Report";
import ContactSupportIcon from "@mui/icons-material/ContactSupport";
import mixpanel from "mixpanel-browser";

const openSupport = () => {
  const initialSupportBtn = document.querySelector(".intercom-launcher");
  mixpanel.track("Support button clicked"); //track event in mixpanel

  if (initialSupportBtn) initialSupportBtn.click();
  else {
    const myFrame = document.querySelector(".intercom-launcher-frame");
    const supportBtn = myFrame.contentWindow.document.querySelector(
      ".intercom-launcher"
    );
    supportBtn.click();
  }
};

const Dashboard = () => (
  <>
    <div className="mainDiv">
      <div className="info">
        <div className="infoText">
          <h3>Get started with Katonic's MLOps Platform</h3>
          <p>
            Katonic empowers machine learning developers, data scientists and
            data engineers to take their projects from ideation to deployment,
            quickly and cost-effectively.&nbsp;
            <span>
              <a
                href="https://learn.katonic.ai/"
                target="_blank"
                rel="noreferrer"
              >
                Learn more
              </a>
            </span>
          </p>
        </div>

        <img className="introImg" src={image} alt="image2" />
      </div>

      <Colxx xxs="12" lg="4" className="mb-4">
        <div className="helpDiv">
          <h3>
            <ForumIcon style={{ color: "#5c31ff" }} />
            <span style={{ marginLeft: "10px" }}>Get in touch</span>
          </h3>
          <p>
            Are you in need of support, did you find a bug, or do you have a
            feature request? Get in touch with us and let us know.
          </p>
          <div className="btnHelpDiv">
            <button className="helpButton report-a-bug">
              <ReportIcon style={{ marginRight: "5px" }} />
              Report a bug
            </button>
            <button onClick={openSupport} className="helpButton">
              <ContactSupportIcon style={{ marginRight: "5px" }} />
              Contact support
            </button>
            <a
              target="_blank"
              rel="noreferrer"
              href="https://katonic.slack.com/"
            >
              <button className="helpButton">
                <img className="slackIcon" src={slackLogo} alt="SlackLogo" />
                <span>Join our Slack Community</span>
              </button>
            </a>
          </div>
        </div>
      </Colxx>

      <Colxx xxs="12" lg="4" className="mb-4">
        <DashboardNotebooks textHead="dashboard.notebooks" />
      </Colxx>
      <Colxx xxs="12" lg="4" className="mb-4">
        <DashboardPipelines textHead="dashboard.pipelines" />
      </Colxx>
      <Colxx xxs="12" lg="4" className="mb-4">
        <DashboardRuns textHead="dashboard.runs" />
      </Colxx>
    </div>
  </>
);
export default Dashboard;
