/* eslint-disable react/no-array-index-key */
import React, { useState, useEffect } from "react";
import axios from "axios";
import { NavLink } from "react-router-dom";
import PerfectScrollbar from "react-perfect-scrollbar";
import { Card, CardBody, CardTitle } from "reactstrap";

import IntlMessages from "../../../helpers/IntlMessages";
import { adminRoot } from "../../../constants/defaultValues";

const DashboardRuns = ({ textHead }) => {
  const [runs, setRuns] = useState([]);

  useEffect(() => {
    const getPipelines = async () => {
      const res = await axios.get("/platform/api/pipeline/runs");
      if (Array.isArray(res.data.runs)) {
        setRuns(res.data.runs);
      }
    };
    getPipelines();
  }, []);
  return (
    <Card className="tickets-container">
      <CardBody>
        <CardTitle>
          <IntlMessages id={textHead} />
        </CardTitle>
        <div className="dashboard-list-with-user">
          <PerfectScrollbar
            options={{ suppressScrollX: true, wheelPropagation: false }}
          >
            {runs.map((ticket, index) => {
              return (
                <div
                  key={index}
                  className="d-flex flex-row mb-3 pb-3 border-bottom"
                >
                  {/* <NavLink to={`/pipeline/#/pipelines/runs/details/${ticket.id}`}>
                    <i className="simple-icon-check img-thumbnail border-0 rounded-circle list-thumbnail align-self-center xsmall" />
                  </NavLink> */}
                  <a
                    target="_blank"
                    rel="noreferrer"
                    href={`/pipeline/#/runs/details/${ticket.id}`}
                  >
                    <i className="simple-icon-check img-thumbnail border-0 rounded-circle list-thumbnail align-self-center xsmall" />
                  </a>

                  <div className="pl-3 pr-2">
                    <a
                      target="_blank"
                      rel="noreferrer"
                      className="notebook-name"
                      href={`/pipeline/#/runs/details/${ticket.id}`}
                    >
                      <p className="font-weight-medium mb-0 ">{ticket.name}</p>
                      <p className="text-muted mb-0 text-small">
                        {ticket.created_at}
                      </p>
                    </a>
                  </div>
                </div>
              );
            })}
          </PerfectScrollbar>
        </div>
      </CardBody>
    </Card>
  );
};
export default DashboardRuns;
