/* eslint-disable react/no-array-index-key */
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import PerfectScrollbar from 'react-perfect-scrollbar';
import { Card, CardBody, CardTitle } from 'reactstrap';
import IntlMessages from '../../../helpers/IntlMessages';
import { getUser } from '../../../helpers/Utils';

const images = {
  vsCodeImgs: ["katonic/vscode:ci", "katonic/vscode:miniconda", "katonic/vscode:miniconda-java11"],
  jupyterLabImages: [
    "katonic/katonic-studio:3.0",
    "katonic/notebook-env:base",
    "katonic/lab-env:torchcpu1.8.1",
    "katonic/lab-env:tf2.4",
    "katonic/lab-env:tf1.4",
    "katonic/lab-env:scikt-learn",
  ],
  jupyterNotebookImages: [
    "katonic/notebook-env:torchcpu1.8.1",
    "katonic/notebook-env:tf2.4",
    "katonic/notebook-env:tf1.4",
    "katonic/notebook-env:scikt-learn"
  ]
}

const WorkspaceConnect = (props) => {
  const image = props.item.stsImageName;
  console.log(image)
  let pathSuffix = "";
  if (images.vsCodeImgs.includes(image)) {
    pathSuffix = "vscode/";
  } else if (images.jupyterNotebookImages.includes(image)) {
    pathSuffix = "";
  } else {
    pathSuffix = "lab?";
  }

  return (
    <>
      <a
        target="_blank"
        rel="noreferrer"
        href={`/${props.item.stsName}-${props.username}/${pathSuffix}`}
      >
        <i className="simple-icon-notebook img-thumbnail border-0 rounded-circle list-thumbnail align-self-center xsmall" />
      </a>
      <div className="pl-3 pr-2 notebook-name">
        <a
          target="_blank"
          rel="noreferrer"
          className="notebook-name"
          href={`/${props.item.stsName}-${props.username}/${pathSuffix}`}
        >
          <p className="font-weight-medium mb-0 ">
            {props.item.stsName}
          </p>
          <p className="text-muted mb-0 text-small">
            {props.item.stsCreateTime}
          </p>
        </a>
      </div>
    </>
  )
}

const DashboardNotebooks = ({ textHead }) => {
  const [data, setData] = useState([]);
  const [username, setUsername] = useState('');

  useEffect(() => {
    getUser()
      .then(res => setUsername(res.userName))
  }, [])

  useEffect(() => {
    const getNotebooks = async () => {
      const res = await axios.get(`/platform/api/notebook`);
      if (Array.isArray(res.data)) {
        setData(res.data);
      }
    };
    getNotebooks();
  }, []);

  return (
    <Card className="tickets-container">
      <CardBody>
        <CardTitle>
          <IntlMessages id={textHead} />
        </CardTitle>
        <div className="dashboard-list-with-user">
          <PerfectScrollbar
            options={{ suppressScrollX: true, wheelPropagation: false }}
          >
            {data.length === 0
              ? null
              : data.map((item, index) => {
                return (
                  <div
                    key={index}
                    className="notebook-name d-flex flex-row mb-3 pb-3 border-bottom"
                  >
                    <WorkspaceConnect username={username} item={item} />
                  </div>
                );
              })}
          </PerfectScrollbar>
        </div>
      </CardBody>
    </Card>
  );
};
export default DashboardNotebooks;
